import React, { useEffect, useRef, useState } from 'react'
import '../StyleCSS/MusicPlay.css'
import Wave from 'react-wavify'
import SuggestMusic from '../Components/SuggestMusic';


function PlayMusic() {

    const [isPlay, setPlay] = useState(false);
    const [currentTime, setCurrentTime] = useState(0);
    const [duration, setDuration] = useState(0);


    function handleSeek(e) {
        audioRef.current.currentTime = e.target.value;
        setCurrentTime(e.target.value);
    }


    function handleTimeUpdate() {
        setCurrentTime(audioRef.current.currentTime);
        setDuration(audioRef.current.duration);
    }

    const audioRef = useRef(null);

    function handlePlayMusic() {
        if (!isPlay) {
            audioRef.current.play();
        } else {
            audioRef.current.pause()
        }
        setPlay(!isPlay);
    }


    useEffect(() => {
        audioRef.current.addEventListener("timeupdate", handleTimeUpdate);
        return () => {
            if (audioRef.current) {
                audioRef.current.removeEventListener("timeupdate", handleTimeUpdate);
            }
        };
    }, [])

    return (
        <div className='play-music-layout container-fluid bg-dark d-flex flex-column align-items-center'>
            <div className='play-frame bg-blur w-25 p-3 rounded-2 mt-4 mb-4'>
                <div className='play-image rounded-2'>
                    <img src={ `${process.env.PUBLIC_URL}${"/Images/Banner8.jpg"}` } alt="" />
                </div>
                <div className='mt-2'>
                    <audio src={ `${process.env.PUBLIC_URL}${"/Audio/JISOO - Your Love (Official Audio).mp4"}` } ref={ audioRef }></audio>
                    <div className='text-left w-100 text-light mb-2'>
                        <h5 className=''>Your Love</h5>
                        <span>JISOO</span>
                        <input
                            type="range"
                            min={ 0 }
                            value={ currentTime }
                            max={ duration }
                            onChange={ handleSeek }
                            className='w-100 mt-1'
                        />
                    </div>
                    <div className='w-100 d-flex justify-content-around align-items-center'>
                        <i class="fa-solid fa-heart"></i>
                        <button
                            className='rounded-5 mt-2 border-0'
                            onClick={ () => handlePlayMusic() }>{ (!isPlay) ? (<i className="fa-solid fa-play"></i>) : (<i className="fa-solid fa-pause"></i>) }
                        </button>
                        <i class="fa-solid fa-volume-high"></i>
                    </div>
                </div>
            </div>

            <div className='w-50 suggest-music-layout mt-4 z-2 p-2 rounded-4'>
               <SuggestMusic/>
            </div>


            <div className='play-footer w-100'>
                <Wave fill='rgb(173,170,199)'
                    paused={ !isPlay }
                    style={ { display: 'flex', height: '100%' } }
                    options={ {
                        height: 20,
                        amplitude: 20,
                        speed: 0.9,
                        points: 3
                    } }
                />

            </div>         
        </div>
    )
}

export default PlayMusic